#devimage{
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
    
    
}

.image{
  
    
 
   border-top-left-radius: 50%;
  border-top-right-radius: 50%;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
    width: 56%;
    justify-items: center;
   
    
}

#dev-dev-img{
    padding-bottom: 30px;
    margin-bottom: 30px;
    position: relative;
}

.game{
    background-color: #ffe998;
    width: 30px;
    height: 30px;
   
    animation-name: square;
    animation-duration: 4s;
    animation-delay: 0.5s;
    animation-iteration-count: infinite;
    animation-direction: normal;
    
}

@keyframes square{
    0%{
        translate: 90px 0;
        background: #ffe998;
        rotate: 0;
    }
    25%{
        translate: 90px 420px;
        background: #57370d;
        rotate: 90deg;
    }
    50%{
        translate: 420px 425px;
        background: #ffe998;
        rotate: 180deg;
    }
    75%{
        translate: 425px 0;
        background: #57370d;
        rotate: 270deg;
    }
    100%{
        translate:90px 0;
        background: #ffe998;
        rotate: 360deg;
    }
}

